/* Set the font family and size */
body {
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f7f7f7;
  text-align: center; /* Add this line to center align text */
  margin-top: 60px;
}

/* Set the max width of the website */
.container {
  max-width: 800px;
  padding: 0 20px;
}

/* Style the header */
header {
  text-align: center;
  margin-top: 30px;
}

header h1 {
  font-size: 36px;
  color: #333;
}

/* Style the sections */
section {
  margin-top: 50px;
  border-top: 1px solid #ddd;
  padding-top: 20px;
}

section h2 {
  font-size: 24px;
  color: #333;
}

section ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

section ul li {
  margin-bottom: 10px;
  color: #666;
}

section p {
  margin-bottom: 10px;
  color: #666;
}

/* Style the links */
a {
  color: #0077ff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Style the lists */
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

ul li {
  margin-bottom: 10px;
  color: #666;
}

/* Add a more professional tone */
.container, header, section {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #333;
}

header h1 {
  font-size: 48px;
}

section h2 {
  font-size: 36px;
}

ul li {
  font-size: 18px;
}

a {
  color: #0077ff;
  text-decoration: none;
  border-bottom: 2px solid #0077ff;
}

a:hover {
  text-decoration: none;
  border-bottom: 2px solid #0057ad;
}

.header-container {
    font-family: "Helvetica Neue", Arial, sans-serif;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
  }
  
  .header-text {
    font-size: 36px;
    color: #fff;
  }
  
form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    margin: 0 auto;
    padding: 2rem;
    border: 2px solid #ccc;
    border-radius: 10px;
  }
  
  label {
    margin: 1rem 0;
    font-size: 1.2rem;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 1rem;
    resize: vertical;
  }
  
  textarea {
    height: 10rem;
  }
  
  button[type="submit"] {
    background-color: #1e90ff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  button[type="submit"]:hover {
    background-color: #0077b6;
  }
  
